import React, { useEffect, useState } from 'react';
import BounceLoader from "react-spinners/BounceLoader";
import './ImageGenerator.css';
import { useReactMediaRecorder } from "react-media-recorder";
// import { WhatsAppShareButton } from 'react-share';

const blobToBase64 = (blob: Blob) => {
    return new Promise((resolve) => {
        const reader = new FileReader();
        reader.readAsDataURL(blob);
        reader.onloadend = function () {
            resolve(reader.result);
        };
    });
};

const ImageGenerator: React.FC = () => {
    const [imageUrl, setImageUrl] = useState<string | null>(null);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [isRecording, setIsRecording] = useState<boolean>(false);
    const [error, setError] = useState<string | null>(null);
    const { startRecording, stopRecording, mediaBlobUrl } =
        useReactMediaRecorder({ audio: true });


    const handleOnSubmit = async () => {
        try {
            if (mediaBlobUrl) {
                let blob = await fetch(mediaBlobUrl).then(r => r.blob());
                const b64 = await blobToBase64(blob);
                setImageUrl(null);
                setIsLoading(true);
                const raw = await fetch("/image", {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    }, method: 'POST', body: JSON.stringify({ blobAsB64: b64 })
                });
                const res = await raw.json();
                setImageUrl(res.url);
            } else {
                console.log('audioBlob is null');
            }
            // setImageUrl(res.url);
            setIsLoading(false);
        } catch (error) {
            console.log(error);
            setError('Hups. Jotain meni pieleen.');
        }
    }

    const handleOnRecord = async () => {
        setIsRecording(!isRecording);
    }

    useEffect(() => {
        if (isRecording) {
            setError(null);
            startRecording();
        }
        if (!isRecording) {
            stopRecording();
        }
    }, [isRecording]);

    return (
        <div className="image-generator">
            <div>Mit haluaisit nhd?</div>
            <button name="record" onClick={handleOnRecord}>{isRecording ? 'Pysyt' : 'Puhu'}</button>
            <button onClick={handleOnSubmit}>Lhet</button>
            {isLoading &&
                <BounceLoader
                    loading={isLoading}
                    size={50}
                    color="#36D7B7"
                />
            }
            {error && <div>{error}</div>}
            {imageUrl &&
                <img src={imageUrl} className="image-generator__image" />
            }
            {/* <WhatsAppShareButton
      url={imageUrl}
      title="Check out this cool image!"
    ></WhatsAppShareButton> */}
        </div>
    );
}

export default ImageGenerator;
